@use '../../../../styles/base/mixins' as *;

@mixin color {
  $light-theme: #faf9fd;

  .grid-container {
    background-color: var(--mat-sys-surface-container-lowest);
  }

  .fetchingDataSpinner {
    @include adjust-color($light-theme);
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
