@use '../../../../../../apps/sanare-app/src/styles/base/mixins' as *;
@use '@angular/material' as mat;

@mixin color {
  $light-skeleton-color: #e3e2e7;

  .basic-table-wrapper {
    background: var(--mat-sys-surface-container-low);
    @include mat.table-overrides(
      (
        background-color: var(--mat-sys-surface-container-low),
        header-headline-color: var(--mat-sys-surface-on-container-low),
        row-item-label-text-color: var(--mat-sys-surface-on-container-low),
      )
    );

    .programs-table {
      .mat-basic-program-table {
        @include mat.paginator-overrides(
          (
            container-background-color: var(--mat-sys-surface-container-low),
          )
        );

        .table-rows:hover {
          background: var(--mat-sys-surface-container-high);
        }
      }

      .mat-basic-program-paginator {
        @include mat.paginator-overrides(
          (
            container-background-color: var(--mat-sys-surface-container-low),
          )
        );
      }
    }
  }

  .programs-table-skeleton {
    .row-skeleton {
      border-bottom-color: var(--mat-sys-surface-container-high);

      div {
        background: var(--mat-sys-surface-container-low);

        @include skeleton-gradient($light-skeleton-color);
      }
    }
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
