@mixin color {
  .wrap-default {
    border: 1px solid var(--mat-sys-surface-container-high);

    .card {
      .card-head {
        background: #ffffff;
      }

      .card-body {
        background: var(--mat-sys-surface-container-high);

        .card-icons {
          .favorite-exercise-star {
            color: #fecb33;
          }
        }
      }
    }

    &:hover {
      outline-offset: 2px;
      outline: 2px solid var(--mat-sys-outline);
    }
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
