/* @use '@angular/material' as mat;
$regular-font-family: 'Inter', sans-serif;
$heading-font-family: 'Poppins', sans-serif;

$sanareapp-typography: (
  plain-family: $regular-font-family,
  brand-family: $heading-font-family,
  //   use-system-variables: true,
    system-variables-prefix: sys,
);
 */

@mixin my-typography-hierarchy {
  // Display styles
  /* h1 {
    font: var(--mat-sys-display-large);
    letter-spacing: var(--mat-sys-display-large-tracking);
    margin: 0 0 0.5em;
  } */

  h2 {
    font: var(--mat-sys-headline-small);
    // letter-spacing: var(--mat-sys-display-medium-tracking);
    margin: 0 0 0.5em;
    font-weight: 500;
  }

  h3 {
    font: var(--mat-sys-display-small);
    letter-spacing: var(--mat-sys-display-small-tracking);
    margin: 0 0 0.5em;
  }

  // Headline styles
  h4 {
    font: var(--mat-sys-headline-large);
    letter-spacing: var(--mat-sys-headline-large-tracking);
    margin: 0 0 0.5em;
  }

  h5 {
    font: var(--mat-sys-headline-medium);
    letter-spacing: var(--mat-sys-headline-medium-tracking);
    margin: 0 0 0.5em;
  }

  h6 {
    font: var(--mat-sys-headline-small);
    letter-spacing: var(--mat-sys-headline-small-tracking);
    margin: 0 0 0.5em;
  }

  // Body styles
  body {
    font: var(--mat-sys-body-large);
    letter-spacing: var(--mat-sys-body-large-tracking);

    p {
      margin: 0 0 7px;
    }
  }

  // Additional body classes
  .mat-body-small {
    font: var(--mat-sys-body-small);
    letter-spacing: var(--mat-sys-body-small-tracking);
  }

  .mat-body-small-print {
    font: var(--mat-sys-body-small);
    letter-spacing: var(--mat-sys-body-small-tracking);
    line-height: 1;
  }

  .mat-body-medium {
    font: var(--mat-sys-body-medium);
    letter-spacing: var(--mat-sys-body-medium-tracking);
  }

  .mat-body-medium-secondary {
    font: var(--mat-sys-body-medium);
    letter-spacing: var(--mat-sys-body-medium-tracking);
    color: var(--mat-sys-outline);
  }

  .mat-body-large {
    font: var(--mat-sys-body-large);
    letter-spacing: var(--mat-sys-body-large-tracking);
  }

  // Additional display classes
  .mat-display-small {
    font: var(--mat-sys-display-small);
    letter-spacing: var(--mat-sys-display-small-tracking);
  }

  .mat-display-medium {
    font: var(--mat-sys-display-medium);
    letter-spacing: var(--mat-sys-display-medium-tracking);
  }

  .mat-display-large {
    font: var(--mat-sys-display-large);
    letter-spacing: var(--mat-sys-display-large-tracking);
  }

  // Additional headline classes
  .mat-headline-small {
    font: var(--mat-sys-headline-small);
    letter-spacing: var(--mat-sys-headline-small-tracking);
  }

  .mat-headline-medium {
    font: var(--mat-sys-headline-medium);
    letter-spacing: var(--mat-sys-headline-medium-tracking);
  }

  .mat-headline-large {
    font: var(--mat-sys-headline-large);
    letter-spacing: var(--mat-sys-headline-large-tracking);
  }

  // Additional label classes
  .mat-label-small {
    font: var(--mat-sys-label-small);
    letter-spacing: var(--mat-sys-label-small-tracking);
  }

  .mat-label-medium {
    font: var(--mat-sys-label-medium);
    letter-spacing: var(--mat-sys-label-medium-tracking);
  }

  .mat-label-large {
    font: var(--mat-sys-label-large);
    letter-spacing: var(--mat-sys-label-large-tracking);
    // font-weight: 500;
  }

  // Additional title classes
  .mat-title-small {
    font: var(--mat-sys-title-small);
    letter-spacing: var(--mat-sys-title-small-tracking);
  }

  .mat-title-medium {
    font: var(--mat-sys-title-medium);
    letter-spacing: var(--mat-sys-title-medium-tracking);
  }

  .mat-title-medium-margin-plus {
    font: var(--mat-sys-body-large);
    letter-spacing: var(--mat-sys-body-large-tracking);
    font-size: 20px;
    margin: 0 0 1rem;
  }

  .mat-title-large {
    font: var(--mat-sys-title-large);
    letter-spacing: var(--mat-sys-title-large-tracking);
    // margin: 0 0 1rem;
  }
  .mat-title-large-margin-plus {
    font: var(--mat-sys-title-large);
    letter-spacing: var(--mat-sys-title-large-tracking);
    margin: 0 0 1rem;
  }
}
