@use '../../../../../../apps/sanare-app/src/styles/base/mixins' as *;
@use '../../../styles/base/mixins' as *;

@mixin color {
  $light-skeleton-color: #e3e2e7;

  .client-progress-container {
    background: var(--mat-sys-surface-container-low);

    @include respond(compact) {
      background: var(--mat-sys-surface-container-lowest);
    }
  }

  .programs-list-wrapper-placeholder {
    .program-list-skeleton {
      background: var(--mat-sys-surface-container-low);

      @include skeleton-gradient($light-skeleton-color);
    }
  }

  .favorites-field-skeleton {
    background: var(--mat-sys-surface-container-low);

    @include skeleton-gradient($light-skeleton-color);
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
