@use '@angular/material' as mat;

@mixin color {
  .program-panel {
    @include mat.expansion-overrides(
      (
        container-background-color: var(--mat-sys-surface-container-low),
      )
    );
  }

  .label {
    color: var(--mat-sys-outline);
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
