@use '@angular/material' as mat;

@mixin color {
  .edit-program-flex {
    .content-wrapper {
      background: var(--mat-sys-surface-container-low);
    }
  }

  .expansion-panel-handle {
    color: var(--mat-sys-outline);
  }
}

