@use 'sass:map';
@use '@angular/material' as mat;
@use './styles/themes/mat19_theme-colors.scss' as sanareapp-theme;
@use './styles/typography/config' as *;
@use './styles/base/mixins' as *;
@use './styles/components';
@use './styles/util/custom_webkit-scrollbars.scss';
@use '../../../styles/sanare-app' as *;
@import 'overlayscrollbars/overlayscrollbars.css';

html {
  color-scheme: light;
  @include mat.theme(
    (
      color: (
        primary: sanareapp-theme.$primary-palette,
        tertiary: sanareapp-theme.$tertiary-palette,
      ),
      typography: (
        plain-family: Inter,
        brand-family: Poppins,
      ),
      density: 0,
    )
  );

  @include my-typography-hierarchy;
  @include components.theme;
}

:root {
  // Clients and programs list tables
  @include mat.table-overrides(
    (
      background-color: var(--mat-sys-surface-container-lowest),
      header-headline-color: var(--mat-sys-surface-on-container-lowest),
      row-item-label-text-color: var(--mat-sys-surface-on-container-lowest),
    )
  );

  @include respond(compact) {
    @include mat.table-overrides(
      (
        background-color: var(--mat-sys-surface-container-lowest),
        header-headline-color: var(--mat-sys-surface-on-container-lowest),
        row-item-label-text-color: var(--mat-sys-surface-on-container-lowest),
      )
    );
  }

  .table-rows:hover {
    background: var(--mat-sys-surface-container-low);

    @include respond(compact) {
      background: inherit;
    }
  }

  // Clients and programs tables paginators
  @include mat.paginator-overrides(
    (
      container-background-color: var(--mat-sys-surface-container-lowest),
    )
  );

  // Ngx-mat-select-search transparent input field fix(including all mat-select elements)
  @include mat.select-overrides(
    (
      panel-background-color: var(--mat-sys-surface-container),
    )
  );

