  } */
}

/*

BELLOW ARE OLD STYLES, IT SHOULD BE REFACTORED AND CUSTOM STYLING OF MAT COMPOENENTS SHOULD BE REMOVED OR REFACTORED CORRECTLY.

ALSO, ALL COMPONENTS STYLES SHOULD BE REFACTORED FROM GLOBAL TO LOCAL AND WITH SEPARATE COMPONENT THEME FILE.

*/

/* You can add global styles to this file, and also import other style files */

html,
body {
  height: 100%;
}
body {
  margin: 0;
  // font-family: Inter, 'Helvetica Neue', sans-serif;

  @include respond(compact) {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
}
