.confirmation-dialog-wrapper {
  height: 736px;
  border-radius: 28px;
}

.dialog-content-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  .modal-img-wrapper {
    width: 70%;
    margin: 0 auto;
    padding-bottom: 24px;
  }

  .category-labels {
    font-size: 16px;
    margin-bottom: 16px;
  }
}

.modal-img {
  border-radius: 16px;
  width: 100%;
}

.exercise-modal-actions {
  justify-content: flex-end !important;
  padding: 24px !important;
}

.title-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 16px;
}

.program-exercise {
  padding: 16px;
}
