@use '../../../../styles/base/mixins' as *;

@mixin color {
  $light-skeleton-color: #e3e2e7;

  .video-skeleton-wrapper {
    .video-skeleton {
      background: var(--mat-sys-surface-container-low);

      @include skeleton-gradient($light-skeleton-color);
    }
  }

  .exercise-video-wrapper {
    // @include mat.elevation(0);
    // transition: all 0.25s linear;

    // border: 1px solid var(--mat-sys-surface-container-highest); // background: var(--mat-sys-surface-container-low);
  }

  .dialog-content-wrapper,
  .programs-wrapper {
    background: var(--mat-sys-surface-container-low);

    @include respond(compact) {
      background: var(--mat-sys-surface-container-lowest);
    }
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
