@use '@angular/material' as mat;

@mixin color {
  .mat-drawer-container {
    background: var(--mat-sys-surface-container-low);
  }

  .filter-drawer {
    background: var(--mat-sys-surface-container-lowest);

    .drawer-wrapper {
      background: var(--mat-sys-surface-container-lowest);
    }
  }

  .mat-drawer-content {
    background: var(--mat-sys-surface-container-lowest);
  }

  mat-stepper {
    @include mat.stepper-overrides(
      (
        container-color: var(--mat-sys-surface-container-lowest),
      )
    );
  }

  .mat-step-header[aria-selected='true'] {
    background: var(--mat-sys-surface-container-high);
  }

  .print-template-container {
    .program-client-container {
      .info-wrapper {
        span:first-of-type {
          font-size: 18px;
          font-weight: 500;
        }

        span {
          font-size: 18px;
          font-weight: 400;
        }
      }
    }

    .print-grid-table {
      .exercise-cell {
        background-color: white;

        .title {
          border-bottom: 1px solid black;

          & > span {
            font-size: 14px;
          }
        }

        .exercise-info-wrapper {
          border-top: 1px solid black;

          .sets-reps {
            & span {
              font-size: 14px;
            }
          }

          .notes {
            & > span {
              font-size: 14px;
            }
          }
        }
      }
    }
  }

  // Print template
  @media print {
    .print-template-container {
      & * {
        font-family: Roboto, sans-serif !important;
      }

      .program-client-container {
        .info-wrapper {
          span:first-of-type {
            font-size: 18px;
            font-weight: 500;
          }

          span {
            font-size: 18px;
            font-weight: 400;
          }
        }
      }

      .print-grid-table {
        .exercise-cell {
          background-color: white;

          .title {
            border-bottom: 1px solid black;

            & > span {
              font-size: 14px;
            }
          }

          .exercise-info-wrapper {
            border-top: 1px solid black;

            .sets-reps {
              border-bottom: 1px solid black;

              & span {
                font-size: 14px;
              }
            }

            .notes {
              & > span {
                font-size: 14px;
              }
            }
          }
        }
      }
    }
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
