        flex: 1 1 2rem;
        // max-height: 2rem;

        .multi-line-text {
          display: -webkit-box; /* Use a flexible box layout model */
          -webkit-box-orient: vertical; /* Vertical layout */
          -webkit-line-clamp: 4; /* Limits text to 3 lines */
          line-clamp: 4; /* For compatibility */
          overflow: hidden; /* Hides any overflowed text */
          text-overflow: ellipsis; /* Optional: Adds ellipsis (...) */
        }
      }

      .card-icons {
        align-items: center;
        justify-content: space-around;
        display: flex;
        padding: 0 0.5rem;
        width: 100%;

