@use '@angular/material' as mat;

@mixin color {
  .login-register {
    background: var(--mat-sys-surface-container-high);

    .login-panel {
      background: var(--mat-sys-surface-container-lowest);

      .guest-login-button {
        @include mat.button-overrides(
          (
            filled-container-color: var(--mat-sys-tertiary-fixed),
            filled-label-text-color: var(--mat-sys-on-tertiary-fixed),
          )
        );
      }
    }
  }

  .error {
    color: var(--mat-sys-error);
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
