@use '../../../../styles/base/mixins' as *;

@mixin color {
  $light-skeleton-color: #e3e2e7;

  .selected-exercises {
    background: var(--mat-sys-surface-container-low);
    color: var(--mat-sys-on-surface);

    .add-exercise-placeholder {
      color: var(--mat-sys-outline);
    }
  }

  .search-segmented-btns {
    background: var(--mat-sys-surface-container-lowest);
  }

  .edit-client-name {
    color: var(--mat-sys-outline);
  }

  .edit-program-name {
    color: var(--mat-sys-outline);
  }

  .exercise-card-placeholder {
    background: var(--mat-sys-surface-container-highest);
  }

  .exercise-card-preview {
    border: 1px solid var(--mat-sys-surface-container-highest);
  }

  .selected-cards-box {
    background: var(--mat-sys-surface);
    border-bottom: solid 1px var(--mat-sys-surface-dim);
  }

  .exercise-card-skeleton {
    background: var(--mat-sys-surface-container-low);

    @include skeleton-gradient($light-skeleton-color);
  }
  //   }
  // }

  .background-wrapper-placeholder {
    .main-wrap-placeholder {
      background: var(--mat-sys-surface-container-low);

      @include skeleton-gradient($light-skeleton-color);
    }
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
