@use 'sass:map';
@use '@angular/material' as mat;

@mixin color {
  .mat-toolbar {
    background: var(--mat-sys-surface-container-low);
  }

  .sidenav {
    background: var(--mat-sys-surface-container-low);
  }

  .navigation-sidenav {
    background: var(--mat-sys-surface-container-low);

    .main {
      background: var(--mat-sys-surface-container-lowest);
    }
  }

  .custom-nav {
    .nav-drawer {
      .nav-list-wrapper {
        .active-link {
          background: var(--mat-sys-secondary-container);
        }

        .nav-list {
          mat-icon {
            color: var(--mat-sys-on-secondary-container);
          }

          &:hover {
            background: var(--mat-sys-secondary-container);
          }

          &:visited {
            color: var(--mat-sys-on-secondary-container);
          }
        }
      }
    }

    &.expanded-custom-nav {
      .nav-drawer {
        .nav-list-wrapper {
          .nav-list {
            &:hover {
              background: var(--mat-sys-secondary-container);
            }
          }
        }
      }
    }
  }

  footer {
    color: var(--mat-sys-on-secondary-container);
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
