.edit-title {
  padding-top: 8px;
  padding-left: 8px;

  span {
    padding-left: 0;
  }
}

.edit-program-flex {
  // padding-bottom: 42px;
  display: flex;
  flex-direction: row;
  gap: 16px;

  .content-wrapper {
    height: calc(100vh - 246px);
    overflow: hidden;
    flex: 2;

    // Experimenting with style
    padding: 16px;
    border-radius: 16px;
    // background: #faf9fd;

    //**************************************//
    // This is the way Google solved this?? Try to implement!!!
    // border-radius: 16px;
    // margin-bottom: 16px;
    // overflow-y: hidden;
    //**************************************//

    .selected-expansion-panel-wrapper {
      // height: calc(100vh - 320px);
      // overflow-y: scroll;
      // display: inline-block;
      // width: 100%;
    }

    /*   .selected-exercises-title {
      // padding: 16px;
      font-size: 18px;
    } */

    .expanded-panels {
      padding: 8px;
      height: 80px;

      .selected-exercise-img {
        height: 100%;
      }
    }
  }

  .program-info-wrapper {
    flex: 1;

    .program-info-content {
      // background-color: #ffffff;
      // border-radius: 16px;
      // padding: 16px;
      // padding-top: 0;
      // padding-left: 0;
      // height: calc(100vh - 232px);

      .program-inputs {
        width: 100%;
        margin-bottom: 8px;

        .mat-mdc-form-field-subscript-wrapper {
          height: auto;
        }
      }

      .print-panel {
        box-shadow: none;

        .select-preview-wrapper {
          display: flex;
          flex-direction: row;
          gap: 8px;

          .pdf-layout {
            width: 100%;
          }

          .preview-btn {
            margin-top: 2px;
          }
        }

        // .custom-btn {
        //   border-radius: 100px;
        //   float: right;
        //   margin-top: 8px;
        // }

        .mat-expansion-panel-body {
          padding: 0;
        }
        mat-expansion-panel-header {
          padding: 0 16px;

          mat-panel-title {
            flex-basis: fit-content;
            // font-size: 16px;
          }
        }
      }
    }
  }
}

.expansion-panel-placeholder {
  min-height: 80px;
}

.exer-expansion-panel {
  padding: 0;
  pointer-events: auto;
  // margin-left: -16px !important;
  cursor: auto;
  // border-bottom: solid 1px #ccc !important;
  // box-shadow: none !important;

  &:hover {
    .expansion-panel-handle {
      mat-icon {
        visibility: visible;
      }
    }
  }

  .exer-expansion-panel-header {
    padding-left: 46px;

    mat-panel-title {
      flex-basis: 150px;
      margin-right: 32px;
      margin-left: 16px;
      // align-items: flex-start;

      .title-name {
        // padding-top: 16px;
      }
    }

    .panel-description {
      padding-right: 8px;

      .session-overview {
        display: flex;
        gap: 50px;
        flex: 1;
      }

      .options-wrapper {
        // color: black;
        align-self: center;
        padding-top: 4px;
      }
    }
  }

  .panel-content {
    display: flex;
    gap: 16px;
    padding-top: 16px;
    padding-left: 30px;
    justify-content: space-evenly;

    // & * {
    //   flex-grow: 1;
    // }

    .name-description-label {
      display: flex;
      flex-direction: column;
      gap: 16px;
      // width: 30%;
      flex: 3;

      .mat-mdc-form-field-subscript-wrapper {
        height: 0;
      }

      .description-text-area {
        // width: 100%;
      }

      // span {
      //   color: rgba(0, 0, 0, 0.6);
      //   margin-bottom: 8px;
      // }
    }

    .exercise-session {
      flex: 1;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      gap: 16px;

      .panel-input {
        .mat-mdc-form-field-subscript-wrapper {
          height: 0;
        }
      }
    }
  }

  .expansion-panel-handle {
    position: absolute;
    top: 28px;
    left: 0px;
    cursor: move;
    // color: #ccc;
    width: 28px;
    height: 28px;
    padding: 5px;
    margin-left: 8px;
    display: flex;
    align-items: center;

    .handle-number {
      margin-right: 10px;
      margin-top: -5px;
    }

    mat-icon {
      visibility: hidden;
    }
  }
}

.authorize-click {
  pointer-events: auto;
}
button.toggle-panel {
  pointer-events: auto;
  align-self: center;
}

@media (max-width: 1060px) {
  .edit-program-flex {
    // height: 200px;
    flex-direction: column-reverse;
    // height: 100%;

    .content-wrapper {
      flex: none;
      // height: calc(100vh - 427px);
      height: calc(100vh - 370px);

      .selected-expansion-panel-wrapper {
        height: calc(100vh - 447px);
      }
    }

    .program-info-wrapper {
      flex: 0;

      .program-info-content {
        // background-color: #ffffff;
        height: fit-content;
        padding: 0;

        .date-input {
          width: 50%;
        }
      }
    }
  }
}
