@use '@angular/material' as mat;
@use '../../../../../../apps/sanare-app/src/styles/base/mixins' as *;

@mixin color {
  .status-toggle {
    .program-list {
      .list-item-wrapper {
        &:hover {
          background: var(--mat-sys-surface-container-high);
        }
      }
    }
    .custom-height-background {
      background: var(--mat-sys-surface-container-low);
    }

    @include respond(compact) {
      .program-list {
        .list-item-wrapper {
          &:hover {
            background: var(--mat-sys-surface-container-low);
          }
        }
      }
      .custom-height-background {
        background: var(--mat-sys-surface-container-lowest);
      }
    }
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
