// /* Global scrollbar styling */
::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-thumb {
  background-color: #8b8b8e; /* Change this to your desired color */
  border-radius: 6px; /* Round the scrollbar edges */
  border: 3px solid transparent; /* Add space around the thumb */
  background-clip: content-box; /* Clip the background to fit the thumb */

  &:hover {
    background-color: #727272;
  }
}

