@use '../../../../../../apps/sanare-app/src/styles/base/mixins' as *;

@mixin color {
  $thumbnail-hover-filter: brightness(0.9);
  $thumbnail-active-filter: brightness(0.8);

  .circle-thumbnail {
    border: 1px solid var(--mat-sys-outline-variant);

    &:hover .thumbnail-img {
      filter: $thumbnail-hover-filter;
    }

    &:active .thumbnail-img {
      filter: $thumbnail-active-filter;
    }
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
