@use '../../../../../../apps/sanare-app/src/styles/base/mixins' as *;

@mixin color {
  $light-skeleton-color: #e3e2e7;

  .clients-program-list-table {
    .clients-table {
      tr.client-program-element-row:not(.client-program-expanded-row):hover {
        background: var(--mat-sys-surface-container);
      }

      tr.client-program-element-row:not(.client-program-expanded-row):active {
        background: var(--mat-sys-surface-container-low);
      }

      .client-program-element-detail {
        .table-client-program-wrapper {
          .list-item {
            border-bottom: 2px solid var(--mat-sys-surface-container-low);

            &:hover {
              background: var(--mat-sys-surface-container);
            }

            .list-item-flex {
              .list-info {
                .list-info-details {
                  color: var(--mat-sys-outline);
                }
              }
            }
          }
        }
      }
    }
  }

  .status-chip-wrapper {
    .status-completed {
      background: var(--mat-sys-secondary-container);
      color: var(--mat-sys-on-secondary-container);
    }

    .status-inprogress {
      background: var(--mat-sys-error-container);
      color: var(--mat-sys-on-error-container);
    }
  }

  .basic-programs-table-placeholder {
    background: var(--mat-sys-surface-container-low);

    .animate-skeleton {
      @include skeleton-gradient($light-skeleton-color);
    }
  }
}

// @mixin typography {
// }

@mixin theme {
  @include color;
  // @include typography;
}
